	import Popups from '@/components/Popups.vue';
	import Kefu from '@/components/Kefu.vue';
	export default {
		components:{
			Popups,
			Kefu
		},
		data() {
			return {
				data: null,
				pay_item: null,
				mobile:''
			}
		},
		onLoad() {
			if(uni.getStorageSync('token')){
				this.submit()
			}
		},
		methods: {
			mobileQuery(){
				this.submit(this.mobile);
			},
			kefu(){
				this.$refs.kefu.show();
			},
			submit(key){
				uni.showLoading({
					title:'查询中'
				})
				this.$http.post('/v5/order/search',{
					keyword:key||''
				}).then(res=>{
					uni.hideLoading();
					if(res.code==1){
						this.data = res.data;
						if(!this.data){
							uni.showToast({
								title: '未查询到相关信息',
								icon: 'none'
							})	
						}
					}else{
						uni.showToast({
							title: res.msg,
							icon: 'none'
						})
					}
					
				})
			},
			payPop(item){
				this.pay_item=item;
				this.$refs.again.show();
			},
			paySubmit(type){
				this.$http.post('/v5/pay/repay',{
					order_sn:this.pay_item.order_sn,
					pay_method: type
				}).then(res=>{
					if(res.code==1){
						if(type=='alipay'){
							location.href = res.data.url;
                            return false;
						}else{	
							localStorage.setItem('pay_result',JSON.stringify(res.data.result));
							location.href = res.data.url;
							return false;
						}
					};
				})
			}
		}
	}
